<script>
import Header from "../../components/global/header/HeaderArea.vue"
import WorkCalendar from '../work/components/WorkCalendar.vue'
import { ref, onMounted } from 'vue'
import { useRouter } from "vue-router"
import { getUrlId,getUrlId2, monthHandle, errorHandle } from '@/utils/handle'
import { apiGetWorkInfo, apiPostTakeJobApply, apiGetResumeList } from "@/api/api"
import { Toast } from '@/components/global/swal'

export default {
    setup() {
        const router = useRouter()
        const urlId = getUrlId2().urlId
        const urlIdLink = `/work/info/${urlId}`
        const resumeId = getUrlId().urlId
        const workInfoData = ref({})

        const postData = {
            "workOrderId": urlId,
            "caretakerResumeId": resumeId,
            "isConfirm": 1,
            "checkTime": [],
            "cancelTime": [],
            "checkDays": [],
            "cancelDays": []
        }
        onMounted(async () => {
            workInfoData.value = await apiGetWorkInfo({ id: urlId })
            workInfoData.value = workInfoData.value.data
            let res = monthHandle(workInfoData.value.workdays)
            workInfoData.value.newDates = res.newDates
            workInfoData.value.info = res.info
            workInfoData.value.time = res.time
            workInfoData.value.monthInfo = Object.keys(workInfoData.value.info)
            // console.log(workInfoData.value)

            //取得履歷
            let resumeList = await apiGetResumeList()
            // console.log(resumeList.data)


            //languageInfo
            workInfoData.value.employerService.languageInfo = []
            if (workInfoData.value.employerService.language?.length > 0) {
                workInfoData.value.employerService.language.forEach(item => {
                    workInfoData.value.employerService.languageInfo.push(item.name)
                })
            }
            //eatTypeInfo
            // workInfoData.value.disabledPerson.eatTypeInfo = []
            // if (workInfoData.value.employerService.eatType?.length > 0) {
            //     workInfoData.value.disabledPerson.eatType.forEach(item => {
            //         workInfoData.value.disabledPerson.eatTypeInfo.push(item.name)
            //     })
            // }
            //abilityToActInfo
            workInfoData.value.disabledPerson.abilityToActInfo = []
            if (workInfoData.value.employerService.abilityToAct?.length > 0) {
                workInfoData.value.disabledPerson.abilityToAct.forEach(item => {
                    workInfoData.value.disabledPerson.abilityToActInfo.push(item.name)
                })
            }
            workInfoData.value.employerService.serviceInfo = []
            if (workInfoData.value.employerService.service?.length > 0) {
                workInfoData.value.employerService.service.forEach(item => {
                    workInfoData.value.employerService.serviceInfo.push(item.name)
                })
            }
            // console.log(userId)
        })

        //點擊日期
        const updateDay = async (val) => {
            // console.log('updateDay', val)
            postData.checkDays = val
        }
        const updateCancelDay = async (val) => {
            // console.log('updateCancelDay', val)
            postData.cancelDays = val
        }

        const apply = async () => {
            let start = workInfoData.value.workdays[0].start.split(' ')[1]
            let end = workInfoData.value.workdays[0].end.split(' ')[1]
            if (start != end) {
                postData.checkTime = dayToTime(postData.checkDays, start, end)
                if (postData.cancelDays) {
                    postData.cancelTime = dayToTime(postData.cancelDays, start, end)
                }
            } else {
                postData.checkTime = dayToTime2(postData.checkDays, start, end)
                if (postData.cancelDays) {
                    postData.cancelTime = dayToTime2(postData.cancelDays, start, end)
                }
            }
            // console.log(postData)
            try {
                let msg = await apiPostTakeJobApply(postData)
                if (msg.status == true) {
                    Toast("已送出")
                    goto(`/work/info/${urlId}`)
                } else {
                    let errMessage = errorHandle(msg)
                    Toast(errMessage)
                }
            } catch (error) {
                console.log(error)
            }
        }
        const goto = (val) => {
            router.push({ path: val })
        }

        const dayToTime = (dayList, start, end) => {
            let array = []
            dayList.forEach(item => {
                array.push({
                    "start": `${item} ${start}`,
                    "end": `${item} ${end}`
                })
            })
            return array
        }
        const dayToTime2 = (dayList, start, end) => {
            let array = []
            dayList.forEach(item => {
                let tomorrow = new Date(Date.parse(item) + 1000 * 60 * 60 * 24)
                array.push({
                    "start": `${item} ${start}`,
                    "end": `${tomorrow.getFullYear()}-${tomorrow.getMonth() + 1}-${tomorrow.getDate()} ${end}`
                })
            })
            return array
        }

        return { workInfoData, goto, urlId,urlIdLink, apply, updateDay, updateCancelDay }
    },
    components: { Header, WorkCalendar },
};
</script>

<template>
    <Header title="主動應徵" :backToPath=urlIdLink />
    <div class="workApple" v-if="workInfoData.monthInfo">
        <div class="item-container">
            <div class="item-title">照顧日期時段</div>
            <div class="item-content">
                <div class="main2">
                    <div class="title2">服務日期</div>
                    <WorkCalendar @update-day="updateDay" @update-cancel-day="updateCancelDay" />
                </div>
                <div class="main2">
                    <div class="title2">服務時段</div>
                    <div class="content2">{{ workInfoData.time }}</div>
                </div>
            </div>
        </div>
        <div class="item-container">
            <div class="item-title item-link collapsed" data-bs-toggle="collapse" data-bs-target="#collapse1">
                {{ workInfoData.employerService.name }}</div>
            <div class="item-content collapse" id="collapse1">
                <div class="main1">
                    <div class="title1">性別</div>
                    <div class="content1">{{ workInfoData.employerService.gender == "Male" ? "男性" :
                            (workInfoData.employerService.gender == "Female") ? "女性"
                                :
                                "不指定"
                    }}</div>
                </div>
                <div class="main1">
                    <div class="title1">年資</div>
                    <div class="content1">{{ workInfoData.employerService.seniority.name }}</div>
                </div>
                <div class="main1">
                    <div class="title1">語言</div>
                    <div class="content1">{{ workInfoData?.employerService?.languageInfo?.join('、') }}</div>
                </div>
                <div class="main2">
                    <div class="title2">具備證照</div>
                    <div class="content2" v-if="(workInfoData.employerService?.license?.name)">{{
                            workInfoData.employerService.license.name
                    }}</div>
                    <div class="content2" v-if="(!workInfoData.employerService?.license?.name)">無</div>
                </div>
            </div>
        </div>
        <div class="item-container">
            <div class="item-title item-link collapsed" data-bs-toggle="collapse" data-bs-target="#collapse2">被照顧者資訊
            </div>
            <div class="item-content collapse" id="collapse2">
                <div class="main1">
                    <div class="title1">名稱</div>
                    <div class="content1">{{ workInfoData.disabledPerson.name }}</div>
                </div>
                <div class="main1">
                    <div class="title1">性別</div>
                    <div class="content1">{{ workInfoData.disabledPerson.gender == 'Female' ? '女性' : '男性' }}</div>
                </div>
                <div class="main1">
                    <div class="title1">年齡</div>
                    <div class="content1">{{ workInfoData.disabledPerson.age }}歲</div>
                </div>
                <div class="main1">
                    <div class="title1">身高</div>
                    <div class="content1">{{ workInfoData.disabledPerson.height }}公分</div>
                </div>
                <div class="main1">
                    <div class="title1">體重</div>
                    <div class="content1">{{ workInfoData.disabledPerson.weight }}公斤</div>
                </div>
                <div class="main1">
                    <div class="title1">關係</div>
                    <div class="content1">{{ workInfoData.disabledPerson.relationship }}</div>
                </div>
                <!-- <div class="main2">
                    <div class="title2">是否有傳染力</div>
                    <div class="content2">{{ workInfoData.disabledPerson.isContagious ? "是" : "否" }}</div>
                </div>
                <div class="main2">
                    <div class="title2">飲食類型</div>
                    <div class="content2">{{ workInfoData?.disabledPerson?.eatTypeInfo?.join('、') }}</div>
                </div> -->
                <div class="main2">
                    <div class="title2">行動能力</div>
                    <div class="content2">{{ workInfoData?.disabledPerson?.abilityToActInfo?.join('、') }}</div>
                </div>
                <div class="main2">
                    <div class="title2">照顧地點</div>
                    <div class="content2" v-if="workInfoData.disabledPerson.servicePlace.placeType.id == '居家'">{{
                            workInfoData.disabledPerson.servicePlace.city.name
                    }}{{
        workInfoData.disabledPerson.servicePlace.area.name
}}</div>
                    <div class="content2" v-if="workInfoData.disabledPerson.servicePlace.placeType.id != '居家'">{{
                            workInfoData.disabledPerson.servicePlace.place.name
                    }}</div>
                </div>
            </div>
        </div>
        <div class="item-container">
            <div class="item-title item-link collapsed" data-bs-toggle="collapse" data-bs-target="#collapse3">病症</div>
            <div class="item-content collapse" id="collapse3">
                <div class="main2" v-for="item in workInfoData.disabledPerson.disease" :key="item.name">
                    <div class="title2">{{ item.diseaseName }}</div>
                    <div class="content2">{{ item.symptom?.join('、') }}</div>
                </div>
            </div>
        </div>
        <div class="item-container">
            <div class="item-title item-link collapsed" data-bs-toggle="collapse" data-bs-target="#collapse4">服務需求</div>
            <div class="item-content collapse" id="collapse4">
                <div class="main2">
                    <div class="content2">{{ workInfoData?.employerService?.serviceInfo?.join('、') }}</div>
                </div>
            </div>
        </div>
        <div class="mt-4b btn-container">
            <div class="row">
                <div class="col">
                    <button class="btn btn-outline-primary" type="button" @click="apply">確定應徵</button>
                </div>
                <div class="col">
                    <button class="btn btn-outline-primary" type="button"
                        @click="goto(`/work/info/${urlId}`)">取消返回</button>
                </div>
            </div>
        </div>
    </div>
</template>

<style lang="scss" scoped>
@import "@/assets/custom/config";

.workApple {
    margin-top: $header-height;
    margin-bottom: calc($footer-height + 15px);
    min-height: calc(100vh - $footer-height - 15px - $header-height);
    padding-top: 1.25rem;
    padding-left: 22px;
    padding-right: 22px;
    padding-bottom: 22px;
}

.title {
    font-size: 1.125rem;
    font-weight: 700;
    color: #000000;
    margin-bottom: 16px;
}

.main1 {
    display: flex;
    align-items: center;
    justify-content: flex-start;
    margin-top: 5px;
    margin-bottom: 5px;

    .title1 {
        font-size: 14px;
        font-weight: 400;
        color: #909090;
        margin-right: 14px;

    }

    .content1 {
        font-size: 16px;
        font-weight: 400;
        color: #333333;
    }
}

.main2 {
    margin-top: 10px;
    margin-bottom: 10px;

    .title2 {
        font-size: 14px;
        font-weight: 400;
        color: nth($secondary-color, 1);
    }

    .content2 {
        font-size: 16px;
        font-weight: 400;
        color: #333333;
    }

}
</style>